import React from "react";
import "../index.less";
import { Button, Form, Input, Radio, Upload, Icon } from "antd";
import { FormComponentProps } from "antd/lib/form/Form";
import { testProvider } from "../../../../api/provider/index";
import ImageUploader from "../../../../components/ImageUploader/index";
import CityPicker from "../../../../components/CityPicker/index";
import CertHeader from "../../../../components/CertHeader/index";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

interface IFormComponentProps extends FormComponentProps {
  cityName: string // 所在城市(省市区,|分割)
  companyId: number // 企业ID	Y
  businessEntity:	string	// 企业名称	Y
  businessLicense: string	// 营业执照号	Y
  legalBody: string	// 联系人姓名	Y
  addr:	string	// 企业地址	Y
  businessLicensePic: string	// 营业执照照片	Y
  phone: string	// 联系人手机电话	Y
  cardType: number //	证件类型(1:统一社会信用代码证书;2:组织机构代码证;3:事业单位法人证书)	Y
  companyType: number //	企业类型(1:企业认证;2:政府/事业单位3:个体工商户;4:其他组织)	Y
  adminOnJobProve: string	 // 管理员在职证明URL
}
class Certification extends React.Component<IFormComponentProps> {
  constructor(props: IFormComponentProps) {
    super(props);
    // this.props.form.cardType = 1;
  }
  public test = () => {
    testProvider.isAllowMultilogin().then(res => {
      console.log(res);
    })
  }
  public changeRadio = (val: any) => {
    const v = this.props.form.getFieldsValue();
  }
  public render() {
    const formItemLayout = {
      labelCol: { span: 2 },
      wrapperCol: { span: 16 },
    };
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="mf-cert">
        <CertHeader />
        <div className="mf-cert-content">
          <div className="mf-cert-content__title">编辑企业信息</div>
            <div className="mf-cert-content-form">
              <Form {...formItemLayout}>
              <Form.Item label="企业类型">
                  {getFieldDecorator('type', {})(<span className="mf-cert-content__type">工商个体<a>前往企业信息修改</a></span>)}
                  <div className="mf-cert-content-form__prompt">
                    <span>
                    待经工商行政管理部门登记，有营业执照的个体工商户补充
                    </span>
                  </div>
                </Form.Item>
                <Form.Item label="证件类型">
                  {getFieldDecorator('cardType', {
                    valuePropName: "1"
                  })(<Radio value={1} defaultChecked>多证合一营业执照（原“注册号”字样，调整为18位“统一社会信用代码”）</Radio>)}
                </Form.Item>
                <Form.Item label="营业执照">
                  {getFieldDecorator('businessLicensePic', {
                    rules: [
                      {
                        required: true,
                        message: "请上传企业营业执照"
                      }
                    ]
                  })(<ImageUploader imageUrl={""} />)}
                  <div className="mf-cert-content-form__prompt">
                    <span>
                      照片所有信息需清晰可见，内容真实有效，不得做任何修改。照片支持.jpg .jpeg .bmp .png .gif格式，大小不超过8M。
                    </span>
                  </div>
                </Form.Item>
                <Form.Item label="统一社会信用代码" {...formItemLayout}>
                  {getFieldDecorator('businessLicense', {
                    rules: [
                      {
                        required: true,
                        whitespace: false,
                        message: '请输入统一社会信用代码',
                      },
                    ],
                  })(<div><Input placeholder="请输入统一社会信用代码" maxLength={18} />
                    <div className="mf-cert-content-form__prompt">
                      <span>18位统一社会信用代码</span>
                    </div>
                  </div>)}
                </Form.Item>
                <Form.Item label="企业名称" {...formItemLayout}>
                  {getFieldDecorator('businessEntity', {
                    rules: [
                      {
                        required: true,
                        whitespace: false,
                        message: '请输入企业名称',
                      },
                      {
                        pattern: /^[\u4e00-\u9fa5A-Za-z0-9]{2,32}$/,
                        message: '企业名称限制2-32位，支持中英文、数字',
                      },
                    ],
                  })(<Input placeholder="请输入企业名称" maxLength={32} />)}
                  <div className="mf-cert-content-form__prompt">
                    <span>企业名称长度最多为32个汉字或字母</span>
                  </div>
                </Form.Item>
                <Form.Item label="法人代表" {...formItemLayout}>
                  {getFieldDecorator('legalBody', {
                    rules: [
                      {
                        required: true,
                        whitespace: true,
                        message: '请输入法人代表姓名',
                      },
                      {
                        pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9·]$/,
                        message: '法人代表姓名名限制2-32位，仅支持中英文、数字和·输入',
                      },
                    ],
                  })(<Input placeholder="请输入法人代表姓名" maxLength={32} />)}
                </Form.Item>
                <Form.Item label="所在城市">
                  {getFieldDecorator('cityName', {
                    rules: [
                      {
                        required: true,
                        whitespace: true,
                        message: '请选择所在城市',
                      },
                    ]
                  })(<CityPicker />)}
                </Form.Item>
                <Form.Item label="详细地址" {...formItemLayout}>
                  {getFieldDecorator('addr', {
                    rules: [
                      {
                        required: true,
                        whitespace: true,
                        message: '请输入详细地址',
                      },
                      {
                        pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9·]$/,
                        message: '详细地址姓名名限制2-32位，仅支持中英文、数字和·输入',
                      },
                    ],
                  })(<Input placeholder="请输入详细地址" maxLength={32} />)}
                    <div className="mf-cert-content-form__prompt">
                    <span>请检查与证件上地址严格一致</span>
                  </div>
                </Form.Item>
                <Form.Item label="管理员在职证明">
                  {getFieldDecorator('adminOnJobProve', {
                    rules: [
                      {
                        required: true,
                        message: "请上传管理员在职证明"
                      }
                    ]
                  })(<ImageUploader imageUrl={""} />)}
                  <div className="mf-cert-content-form__prompt">
                    <span>
                    请下载《在职证明》模板 <a>点击下载</a><br/>按要求填写，并手写“仅用于企业萤石云认证”字样，并加盖企业公章。照片支持.jpg .jpeg .bmp .png .gif格式，大小不超过8M。
                    </span>
                  </div>
                </Form.Item>
              </Form>
              <div className="mf-cert-content__reader">
                <Radio>已阅读并同意<a>《企业萤石云XXX协议》</a></Radio>
              </div>
              <div className="mf-cert-content__commit">
                <Button className="mf-button" type="primary" onClick={this.test} size="large" >保存</Button>
              </div>
        </div>

        </div>
      </div>
    );
  }
}

const WapperdCertification = Form.create<IFormComponentProps>()(Certification);
export default WapperdCertification;
